import "./index.css";

const Result = ({ result }) => (
    <p className="form__result">
        {result !== undefined && (
            <>
                {(result.sourceAmount).toFixed(2)}&nbsp;PLN&nbsp;=&nbsp;

                <strong>
                    {(result.targetAmount).toFixed(2)}&nbsp;{result.currency}
                </strong>
            </>
        )}
    </p>
);

export default Result;